  variantMarginTop: PropTypes.string,
  variantIcon: PropTypes.string,
  avatar: PropTypes.bool,
  settings: PropTypes.bool,
  google: PropTypes.bool,
  variantMessage: PropTypes.string,
  variantMarginBottom: PropTypes.string,
